import React, { useEffect, useState, useCallback } from 'react';
import classNames from "classnames";
import Icon from '../../../../../platform/ui/src/components/Icon'
import {Button, useViewportGrid, ListMenu} from "@ohif/ui";
import * as csTools from '@cornerstonejs/tools';
import {useTranslation} from "react-i18next";
const { Enums: csToolsEnums } = csTools
const { Events } = csToolsEnums

class CappedArray extends Array {
    private readonly maxLength: number;

    constructor(maxLength) {
        super();
        this.maxLength = maxLength;
    }

    push(...elements) {
        // Appeler la mthode push de la classe Array parente
        const r = super.push(...elements);

        // Si la longueur dpasse maxLength, retirer les lments les plus anciens
        if (this.length > this.maxLength) {
            const excess = this.length - this.maxLength;
            this.splice(0, excess);
        }

        return r;
    }
}

const MAX_CACHE_LENGTH = 10;
const CACHE_SEGMENTATION_VOLUMES = new CappedArray(MAX_CACHE_LENGTH);

export default function UndoToolEdit ({ segmentationService, cornerstoneViewportService }) {
    const { t } = useTranslation('Buttons');

    const [isMinimized, setIsMinimized] = useState(false);
    const [{ activeViewportId }, viewportGridService] = useViewportGrid();
    const [currentCacheIndex, setCurrentCacheIndex] = useState(CACHE_SEGMENTATION_VOLUMES.length);

    useEffect(() => {
        const viewport = cornerstoneViewportService.getCornerstoneViewport(activeViewportId);
        const currentElement = viewport.element
        // Save previous segmentation states for "undo" functionality
        currentElement.addEventListener(Events.MOUSE_DOWN, evt => {
            const old_segmentation = segmentationService.getSegmentations()[0]
            const old_seg_volume = new Uint8ClampedArray(segmentationService.getLabelmapVolume(old_segmentation.id).getScalarData());

            //Add current segmentation volume in cache to save its state
            if (CACHE_SEGMENTATION_VOLUMES.length === MAX_CACHE_LENGTH) {
                CACHE_SEGMENTATION_VOLUMES.shift();
            }
            CACHE_SEGMENTATION_VOLUMES.push(old_seg_volume);
            setCurrentCacheIndex(CACHE_SEGMENTATION_VOLUMES.length);
        });
    }, []);

    const apply_replace = (cacheIndex) => {
        const viewport = cornerstoneViewportService.getCornerstoneViewport(activeViewportId);
        const currentElement = viewport.element

        const previous_volume = CACHE_SEGMENTATION_VOLUMES[cacheIndex]
        if(!previous_volume) return

        const replace_event = new CustomEvent('UNDO_REDO_TOOL', {
            detail: {
                element: currentElement,
                oldScalarData: previous_volume
            }
        });
        window.dispatchEvent(replace_event);
        setCurrentCacheIndex(cacheIndex);
    }

    const undo = () => {
        if (currentCacheIndex > 0) {
            apply_replace(currentCacheIndex - 1);
        }
    }

    const redo = () => {
        if (currentCacheIndex < MAX_CACHE_LENGTH) {
            apply_replace(currentCacheIndex + 1);
        }
    }

    return (
        <div className="flex flex-col min-h-0 font-inter font-[300]">
            <div className="bg-primary-dark">
                <div
                    className="flex cursor-pointer items-center"
                    onClick={evt => {
                        evt.stopPropagation();
                        setIsMinimized(!isMinimized);
                    }}
                >
                    <Icon
                        name="panel-group-open-close"
                        className={classNames(
                            'w-5 h-5 text-white transition duration-300 cursor-pointer',
                            {
                                'transform rotate-90': !isMinimized,
                            }
                        )}
                    />
                    <span className="text-[#d8d8d8] text-[12px] font-[300]">
                        {'Undo/redo Segmentation'}
                    </span>
                </div>
                {/* active segmentation */}
                {!isMinimized && (
                    <div className="pl-2 pr-2 pb-[9px] pt-[9px]" style={{display:'flex', flexDirection:"column"}}>
                        <div
                            className="text-[#d8d8d8] text-[12px] font-[300] justify-evenly inline-flex"
                        >
                            <Button
                                fullWidth={true}
                                size={'small'}
                                color={'secondary'}
                                onClick={undo}
                            >{'Undo'}</Button>
                            <Button
                                fullWidth={true}
                                size={'small'}
                                color={'secondary'}
                                onClick={redo}
                            >{'Redo'}</Button>
                        </div>
                    </div>
                )}
                <div className="h-[6px] bg-black "></div>
            </div>
        </div>
    );
}
