import React, { useState, useCallback, useEffect } from 'react';
import classNames from 'classnames';
import Typography from '../Typography';
import './DualInputRange.css';
import {Button} from "@ohif/ui";


/**
 * React Range Input component
 * it has two props, value and onChange
 * value is a number value
 * onChange is a function that will be called when the range input is changed
 *
 *
 */

const DualInputRange: React.FC<{
    value1: number;
    value2: number;
    onChange: (value1, value2) => void;
    minValue: number;
    maxValue: number;
    step: number;
    unit?: string;
    containerClassName?: string;
    inputClassName?: string;
    labelClassName?: string;
    labelVariant?: string;
    showLabel?: boolean;
    labelPosition?: string;
    trackColor?: string;
}> = ({
                                value1,
                                value2,
                                onChange,
                                minValue,
                                maxValue,
                                step = 1,
                                unit = '',
                                containerClassName,
                                inputClassName,
                                labelClassName,
                                labelVariant,
                                showLabel = true,
                                labelPosition = '',
                                trackColor,
                            }) => {
    const [rangeValue1, setRangeValue1] = useState(value1);
    const [rangeValue2, setRangeValue2] = useState(value2);

    useEffect(() => {
        //Change value 1
        if(value1 < minValue) return
        setRangeValue1(value1);
        // if (value1 >= rangeValue2) {
        //     setRangeValue2(value1);
        // }

        //Change value 2
        if(value2 > maxValue) return
        setRangeValue2(value2);
        // if (value2 <= rangeValue1) {
        //     setRangeValue1(value2);
        // }
    }, [value1, value2])

    const handleChange1 = (e) => {
        const value = Number(e.target.value);
        if(value < minValue) return
        setRangeValue1(value);
        // if (value >= rangeValue2) {
        //     setRangeValue2(value);
        // }
        onChange(value, rangeValue2)
    };

    const handleChange2 = (e) => {
        const value = Number(e.target.value);
        if(value > maxValue) return
        setRangeValue2(value);
        // if (value <= rangeValue1) {
        //     setRangeValue1(value);
        // }
        onChange(rangeValue1, value)
    }

    const rangeValuePercentage1 = ((rangeValue1 - minValue) / (maxValue - minValue)) * 100;
    const rangeValuePercentage2 = ((rangeValue2 - minValue) / (maxValue - minValue)) * 100;

    const rangeValueForStr1 = step >= 1 ? rangeValue1.toFixed(0) : rangeValue1.toFixed(1);
    const rangeValueForStr2 = step >= 1 ? rangeValue2.toFixed(0) : rangeValue2.toFixed(1);

    return (
        <div
            style={{flexDirection:'column'}}
            className={`flex items-center cursor-pointer space-x-1 ${
                containerClassName ? containerClassName : ''
            }`}
        >
            {showLabel && labelPosition === 'left' && (
                <>
                    <Typography
                        variant={labelVariant ?? 'subtitle'}
                        component="p"
                        className={classNames('w-8', labelClassName ?? 'text-white')}
                    >
                        {rangeValueForStr1}
                        {unit}
                    </Typography>
                    <Typography
                        variant={labelVariant ?? 'subtitle'}
                        component="p"
                        className={classNames('w-8', labelClassName ?? 'text-white')}
                    >
                        {rangeValueForStr2}
                        {unit}
                    </Typography>
                </>
            )}
            <div style={{flexDirection:'row'}} className={'flex pb-[5px]'}>
                <input
                    type="range"
                    min={minValue}
                    max={maxValue}
                    value={rangeValue1}
                    className={`appearance-none h-[3px] rounded-lg input-range-thumb-design ${
                        inputClassName ? inputClassName : ''
                    }`}
                    style={{background: '#3a3f99',}}
                    onChange={handleChange1}
                    id="myRange1"
                    step={step}
                />
                <input
                    type="range"
                    min={minValue}
                    max={maxValue}
                    value={rangeValue2}
                    className={`appearance-none h-[3px] rounded-lg input-range-thumb-design ${
                        inputClassName ? inputClassName : ''
                    }`}
                    style={{background: '#3a3f99',}}
                    onChange={handleChange2}
                    id="myRange2"
                    step={step}
                />
            </div>
            {showLabel && (!labelPosition || labelPosition === 'right') && (
                <div style={{flexDirection:'row', width:'100%'}} className={'flex pb-[5px]'}>
                    <Typography
                        variant={labelVariant ?? 'subtitle'}
                        component="p"
                        className={classNames('w-1/2', labelClassName ?? 'text-white', 'flex', 'justify-center')}
                    >
                        {'Min : '}
                        {rangeValueForStr1}
                        {unit}
                    </Typography>
                    <Typography
                        variant={labelVariant ?? 'subtitle'}
                        component="p"
                        className={classNames('w-1/2', labelClassName ?? 'text-white', 'flex', 'justify-center')}
                    >
                        {'Max : '}
                        {rangeValueForStr2}
                        {unit}
                    </Typography>
                </div>
            )}
            <div className={'flex'} style={{width:'100%'}}>
                <div className={'flex w-1/2 justify-center'}>
                    <Button size={'small'} color={'black'} onClick={() => {
                        handleChange1({target: {value: rangeValue1 - 10}})
                    }}>{'--'}</Button>
                    <Button size={'small'} color={'black'} onClick={() => {
                        handleChange1({target: {value: rangeValue1 - 1}})
                    }}>{'-'}</Button>
                    <Button size={'small'} color={'black'} onClick={() => {
                        handleChange1({target: {value: rangeValue1 + 1}})
                    }}>{'+'}</Button>
                    <Button size={'small'} color={'black'} onClick={() => {
                        handleChange1({target: {value: rangeValue1 + 10}})
                    }}>{'++'}</Button>
                </div>
                <div className={'flex w-1/2 justify-center'}>
                    <Button size={'small'} color={'black'} onClick={() => {
                        handleChange2({target: {value: rangeValue2 - 10}})
                    }}>{'--'}</Button>
                    <Button size={'small'} color={'black'} onClick={() => {
                        handleChange2({target: {value: rangeValue2 - 1}})
                    }}>{'-'}</Button>
                    <Button size={'small'} color={'black'} onClick={() => {
                        handleChange2({target: {value: rangeValue2 + 1}})
                    }}>{'+'}</Button>
                    <Button size={'small'} color={'black'} onClick={() => {
                        handleChange2({target: {value: rangeValue2 + 10}})
                    }}>{'++'}</Button>
                </div>
            </div>
        </div>
    );
};

export default DualInputRange;
