import React, { useEffect, useState, useCallback } from 'react';
import classNames from "classnames";
import Icon from '../../../../../platform/ui/src/components/Icon'
import InputRange from '../../../../../platform/ui/src/components/InputRange';
import DualInputRange from '../../../../../platform/ui/src/components/DualInputRange';
import {Button, useViewportGrid, ListMenu} from "@ohif/ui";
import * as csTools from '@cornerstonejs/tools';
import { useTranslation } from 'react-i18next';
const { Enums: csToolsEnums } = csTools
const { MouseBindings, Events } = csToolsEnums;
import { utilities as csUtils } from '@cornerstonejs/core';

const DefaultListItemRenderer = ({ label, activeMode, eraseReplaceFocus }) => {
    const isActive = label === eraseReplaceFocus;
    if (label === 'All' && activeMode === Brush_Mode.REPLACE) {
        label = 'Background';
    }

    return (
        <div
            className={classNames(
                'flex flex-row items-center p-3 h-8 w-full hover:bg-primary-dark',
                'text-base whitespace-pre',
                isActive && 'bg-primary-dark',
                isActive
                    ? 'text-[#348CFD]'
                    : 'text-common-bright hover:bg-primary-dark hover:text-primary-light'
            )}
        >
            <span className="mr-5">{label}</span>
        </div>
    );
};
const listItemRenderer = DefaultListItemRenderer

enum Brush_Tool {
    CIRCLE      =   'CIRCLE',
    SPHERE      =   'SPHERE'
}

enum Brush_Mode {
    FILL        =   'FILL',
    THRESHOLD   =   'THRESHOLD',
    ERASE       =   'ERASE',
    REPLACE     =   'REPLACE'
}

const getBrushStrategy = (tool: Brush_Tool, mode: Brush_Mode) => {
    return mode + '_INSIDE_' + tool;
}

const EVENT_LISTENERS = []

export default function BrushToolEdit ({ segmentationService, cornerstoneViewportService }){
    const { t } = useTranslation('Buttons');
    const [isMinimized, setIsMinimized] = useState(false);
    const [isEraseReplaceOnlyMinimized, setIsEraseReplaceOnlyMinimized] = useState(true);
    const [eraseReplaceFocus, setEraseReplaceFocus] = useState('All');
    const [pipetteMode, setPipetteMode] = useState({active: false, minimum: true});

    // @ts-ignore
    const [{ activeViewportId }, viewportGridService] = useViewportGrid();
    const [activeMode, setActiveMode] = useState(Brush_Mode.FILL);
    const [activeTool, setActiveTool] = useState(Brush_Tool.CIRCLE);
    const [brushSize, setBrushSize] = useState(10);
    // TODO adapt maxBrushSize on spacing
    const [maxBrushSize, setMaxBrushSize] = useState(35);
    const [spacing, setSpacing] = useState(0.4);
    const [circleThresholdValue, setCircleThresholdValue] = useState([-1000, 3095]);
    const [circleTargetSegmentIndex, setCircleTargetSegmentIndex] = useState(0);
    const [segments, setSegments] = useState([]);

    const handleSegmentClick = (args, brushInstance) => {
        const {label, segmentIndex} = args;
        const strategySpecificConfiguration = brushInstance.configuration.strategySpecificConfiguration;
        strategySpecificConfiguration.REPLACE.targetSegmentIndex = segmentIndex;
        brushInstance.setConfiguration({
            eraseFocusIndex: segmentIndex, strategySpecificConfiguration: strategySpecificConfiguration
        });
        setEraseReplaceFocus(label);
    }

    const setSegmentsForToolGroup = (brushInstance) => {
        const segmentation = segmentationService.getSegmentations().filter(s => s.isActive)[0];
        let segments = [{label: 'All', segmentIndex: 0}, ...segmentation.segments.filter(s => s)];
        segments = segments.map(s => ({
            ...s,
            onClick: args => handleSegmentClick(args, brushInstance)
        }));
        setSegments(segments);
    }

    useEffect(() => {
        const viewport = cornerstoneViewportService.getCornerstoneViewport(activeViewportId);
        const toolGroup = csTools.ToolGroupManager.getToolGroupForViewport(viewport.id, viewport.renderingEngineId);
        const brushInstance = toolGroup.getToolInstance('Brush');

        setActiveMode(Brush_Mode.FILL);
        setActiveTool(Brush_Tool.CIRCLE);

        const { volume } = cornerstoneViewportService.getViewportInfo(activeViewportId).viewportData.data[0];
        const spacing = Math.min(...volume.spacing);
        const brushSize = Math.floor(brushInstance.configuration.brushSize / spacing);
        // No greater than half the default dentascan thickness
        const maxBrushSize = Math.min(Math.floor(Math.min(...volume.dimensions) / 2), 35);

        setSpacing(spacing);
        setBrushSize(brushSize);
        setMaxBrushSize(maxBrushSize);
        setCircleThresholdValue(brushInstance.configuration.strategySpecificConfiguration.THRESHOLD.threshold);
        setCircleTargetSegmentIndex(brushInstance.configuration.strategySpecificConfiguration.REPLACE.targetSegmentIndex);
        setSegmentsForToolGroup(brushInstance);

        toolGroup.setToolActive('Undo', { bindings: [{ mouseButton: MouseBindings.Primary }] });
    }, []);

    // Update "erase more" list on segment modified
    useEffect(() => {
        const viewport = cornerstoneViewportService.getCornerstoneViewport(activeViewportId);
        const toolGroup = csTools.ToolGroupManager.getToolGroupForViewport(viewport.id, viewport.renderingEngineId);
        const brushInstance = toolGroup.getToolInstance('Brush');

        const added = segmentationService.EVENTS.SEGMENTATION_ADDED;
        const updated = segmentationService.EVENTS.SEGMENTATION_UPDATED;
        const removed = segmentationService.EVENTS.SEGMENTATION_REMOVED;
        const subscriptions = [];

        [added, updated, removed].forEach(evt => {
            const { unsubscribe } = segmentationService.subscribe(evt, () => {
                setSegmentsForToolGroup(brushInstance);
            });
            subscriptions.push(unsubscribe);
        });

        return () => {
            subscriptions.forEach(unsub => {
                unsub();
            });
        };
    }, []);

    useEffect(() => {
        const viewport = cornerstoneViewportService.getCornerstoneViewport(activeViewportId);
        const toolGroup = csTools.ToolGroupManager.getToolGroupForViewport(viewport.id, viewport.renderingEngineId);
        const brushInstance = toolGroup.getToolInstance('Brush');

        brushInstance.setConfiguration({
            brushSize: brushSize * spacing,
            activeStrategy: getBrushStrategy(activeTool, activeMode),
            strategySpecificConfiguration: {
                THRESHOLD: {
                    threshold: circleThresholdValue, // E.g. CT Fat // Only used during threshold strategies.
                },
                REPLACE: {
                    targetSegmentIndex: circleTargetSegmentIndex, // E.g. Only used during replace strategies.
                },
            },
        });
    }, [brushSize, activeTool, activeMode, circleThresholdValue]);

    const setThresholdCallback = (evt, minimum) => {
        //If left click
        if (evt.detail.mouseButton === 1) {
            const viewport = cornerstoneViewportService.getCornerstoneViewport(activeViewportId);
            const worldPoints = evt.detail.currentPoints.world;
            const imageData = viewport.getImageData();
            const index = imageData.imageData.worldToIndex(worldPoints).map(Math.round);

            const {scalarData, dimensions} = imageData;
            if (csUtils.indexWithinDimensions(index, dimensions)) {
                const yMultiple = dimensions[0];
                const zMultiple = dimensions[0] * dimensions[1];

                const value = scalarData[index[2] * zMultiple + index[1] * yMultiple + index[0]];
                if (minimum) {
                    setCircleThresholdValue([value, circleThresholdValue[1]]);
                } else {
                    setCircleThresholdValue([circleThresholdValue[0], value]);
                }
            }
        }
    }

    //Create event listeners to catch mouse click for pipette threshold tool
    //Active probe tool is pipette active to display HU values on screen
    //Go back to brush tool if we deactivate pipette
    //Wrapper around addEventListener to not have multiple listeners
    useEffect(() => {
        const viewport = cornerstoneViewportService.getCornerstoneViewport(activeViewportId);
        const toolGroup = csTools.ToolGroupManager.getToolGroupForViewport(viewport.id, viewport.renderingEngineId);
        const currentElement = viewport.element;
        currentElement.addEvent = (evt, callback) => {
            if (EVENT_LISTENERS[evt]) {
                currentElement.removeEventListener(evt, EVENT_LISTENERS[evt]);
                EVENT_LISTENERS[evt] = null;
            }
            EVENT_LISTENERS[evt] = callback;
            currentElement.addEventListener(evt, callback);
        }
        currentElement.removeEvent = (evt) => {
            currentElement.removeEventListener(evt, EVENT_LISTENERS[evt]);
            EVENT_LISTENERS[evt] = null;
        }
        currentElement.removeEvent(Events.MOUSE_DRAG);

        if (pipetteMode.active) {
            if (toolGroup.getToolOptions('DragProbe').mode !== csToolsEnums.ToolModes.Active) {
                toolGroup.setToolActive('DragProbe', { bindings: [{ mouseButton: MouseBindings.Primary }] });
                toolGroup.setToolPassive('Brush');
            }
            currentElement.addEvent(Events.MOUSE_DRAG, (evt) => { setThresholdCallback(evt, pipetteMode.minimum) });
        } else {
            toolGroup.setToolActive('Brush', { bindings: [{ mouseButton: MouseBindings.Primary, }] });
            toolGroup.setToolPassive('DragProbe');
        }

        return () => {
            currentElement.removeEvent(Events.MOUSE_DRAG);
        }
    }, [pipetteMode.active, pipetteMode.minimum]);

    return (
        <div className="flex flex-col min-h-0 font-inter font-[300]">
            <div className="bg-primary-dark">
                <div
                    className="flex cursor-pointer items-center"
                    onClick={() => setIsMinimized(!isMinimized)}
                >
                    <Icon
                        name="panel-group-open-close"
                        className={classNames(
                            'w-5 h-5 text-white transition duration-300 cursor-pointer',
                            {
                                'transform rotate-90': !isMinimized,
                            }
                        )}
                    />
                    <span className="text-[#d8d8d8] text-[12px] font-[300]">
                        {'Paint Brush Tool Edit'}
                    </span>
                </div>
                {/* active segmentation */}
                {!isMinimized && (
                    <div className="pl-2 pr-2 pb-[9px]" style={{display:'flex', flexDirection:"column"}}>
                        <span className="text-[#d8d8d8] text-[12px] font-[300] pb-[9px]">
                            {'Tool and mode'}
                        </span>
                        <div
                            className="text-[#d8d8d8] text-[12px] font-[300] pb-[9px]"
                            style={{display:'inline-flex', justifyContent:'space-evenly'}}
                        >
                            <Button
                                fullWidth={true}
                                size={'small'}
                                color={activeTool === Brush_Tool.CIRCLE ? 'primary' : 'secondary'}
                                onClick={() => {setActiveTool(Brush_Tool.CIRCLE)}}
                            >{'Circle'}</Button>
                            <Button
                                fullWidth={true}
                                size={'small'}
                                color={activeTool === Brush_Tool.SPHERE ? 'primary' : 'secondary'}
                                onClick={() => {setActiveTool(Brush_Tool.SPHERE)}}
                            >{'Sphere'}</Button>

                        </div>
                        <div
                            className="text-[#d8d8d8] text-[12px] font-[300] pb-[9px]"
                            style={{display:'inline-flex', justifyContent:'space-evenly'}}
                        >
                            <Button
                                fullWidth={true}
                                size={'small'}
                                color={activeMode === Brush_Mode.FILL ? 'primary' : 'secondary'}
                                onClick={() => {setActiveMode(Brush_Mode.FILL)}}
                            >{'Fill'}</Button>
                            <Button
                                fullWidth={true}
                                size={'small'}
                                color={activeMode === Brush_Mode.THRESHOLD ? 'primary' : 'secondary'}
                                onClick={() => {setActiveMode(Brush_Mode.THRESHOLD)}}
                            >{'Threshold'}</Button>
                            <Button
                                fullWidth={true}
                                size={'small'}
                                color={activeMode === Brush_Mode.ERASE ? 'primary' : 'secondary'}
                                onClick={() => {setActiveMode(Brush_Mode.ERASE)}}
                            >{'Erase'}</Button>
                            <Button
                                fullWidth={true}
                                size={'small'}
                                color={activeMode === Brush_Mode.REPLACE ? 'primary' : 'secondary'}
                                onClick={() => {setActiveMode(Brush_Mode.REPLACE)}}
                            >{'Replace'}</Button>
                        </div>
                        <div className="flex items-center col-span-2">
                            <div className="text-[#ffffff] text-[12px] pr-[9px]" style={{width:'100%'}}>{'Radius'}</div>
                            <InputRange
                                minValue={0}
                                maxValue={maxBrushSize}
                                value={brushSize}
                                onChange={(value) => {setBrushSize(value)}}
                                step={1}
                                containerClassName="mt-[4px] mb-[4px]"
                                inputClassName="w-[64px]"
                                labelClassName="text-white text-[12px]"
                                unit="px"
                            />
                        </div>
                        <div className="flex flex-col min-h-0 font-inter font-[300]">
                            <div className="bg-primary-dark">
                                <div
                                    className="flex cursor-pointer items-center"
                                    onClick={evt => {
                                        evt.stopPropagation();
                                        setIsEraseReplaceOnlyMinimized(!isEraseReplaceOnlyMinimized);
                                    }}
                                >
                                    <Icon
                                        name="panel-group-open-close"
                                        className={classNames(
                                            'w-5 h-5 text-white transition duration-300 cursor-pointer',
                                            {
                                                'transform rotate-90': !isEraseReplaceOnlyMinimized,
                                            }
                                        )}
                                    />
                                    <span className="text-[#d8d8d8] text-[12px] font-[300]">
                                        {'Erase/replace options'}
                                    </span>
                                </div>
                                {/* active segmentation */}
                                {!isEraseReplaceOnlyMinimized && (
                                    <div className="pl-2 pr-2" style={{display:'flex', flexDirection:"column"}}>
                                        <span className="text-[#d8d8d8] text-[12px] font-[300] pb-[6px]">
                                            {'Erase/replace focus on:'}
                                        </span>
                                        <ListMenu
                                            items={segments}
                                            renderer={args => listItemRenderer({...args, activeMode, eraseReplaceFocus})}
                                        />
                                    </div>
                                )}
                            </div>
                        </div>
                        {
                            activeMode === Brush_Mode.THRESHOLD && (
                                <div className="flex items-center col-span-2" style={{flexDirection:'column', width:'100%'}}>
                                    <div className="h-[1px] bg-[#212456] mb-[8px] mx-1" style={{width: '100%'}}></div>
                                    <div className="text-[#ffffff] text-[12px] pr-[9px]" style={{width:'100%'}}>
                                        {'Threshold values'}
                                    </div>
                                    <DualInputRange
                                        minValue={-1000}
                                        maxValue={3095}
                                        value1={circleThresholdValue[0]}
                                        value2={circleThresholdValue[1]}
                                        onChange={(vmin, vmax) => {setCircleThresholdValue([vmin, vmax])}}
                                        step={5}
                                        containerClassName="mt-[4px] mb-[4px] w-full"
                                        inputClassName="w-full"
                                        labelClassName="text-white text-[12px] w-full"
                                        unit="HU"
                                    />
                                    <div className="text-[#ffffff] text-[12px] pr-[9px] pt-[9px] mb-[3px]" style={{width:'100%'}}>
                                        {'Select threshold value with pipette for :'}
                                    </div>
                                    <div className="flex flex-row justify-evenly w-full mb-[8px]">
                                        <Button
                                            fullWidth={true}
                                            size={'small'}
                                            color={'secondary'}
                                            onClick={() => { setPipetteMode({active: true, minimum: true})}}
                                        >{'Minimum'}</Button>
                                        <Button
                                            fullWidth={true}
                                            size={'small'}
                                            color={'secondary'}
                                            onClick={() => { setPipetteMode({active: true, minimum: false})}}
                                        >{'Maximum'}</Button>
                                    </div>
                                    {
                                        pipetteMode.active && (
                                            <Button
                                                size={'small'}
                                                color={'secondary'}
                                                onClick={() => { setPipetteMode({active: false, minimum: true})}}
                                            >{'Back to annotation'}</Button>
                                        )
                                    }
                                </div>
                            )
                        }
                    </div>
                )}
                <div className="h-[6px] bg-black "></div>
            </div>
        </div>
    );
}
