import React, { useEffect, useState, useCallback } from 'react';
import classNames from "classnames";
import Icon from '../../../../../platform/ui/src/components/Icon'
import {Button, useViewportGrid, ListMenu} from "@ohif/ui";
import * as csTools from '@cornerstonejs/tools';
import {useTranslation} from "react-i18next";

const DefaultListItemRenderer = ({ label, t, eraseFocus }) => {
    const isActive = label === eraseFocus;

    return (
        <div
            className={classNames(
                'flex flex-row items-center p-3 h-8 w-full hover:bg-primary-dark',
                'text-base whitespace-pre',
                isActive && 'bg-primary-dark',
                isActive
                    ? 'text-[#348CFD]'
                    : 'text-common-bright hover:bg-primary-dark hover:text-primary-light'
            )}
        >
            <span className="mr-5">{label}</span>
        </div>
    );
};
const listItemRenderer = DefaultListItemRenderer

enum Sphere_Strategies {
    FILL_INSIDE = 'FILL_INSIDE',
    ERASE_INSIDE = 'ERASE_INSIDE'
}

export default function SphereScissorsToolEdit({ segmentationService, cornerstoneViewportService }) {
    const { t } = useTranslation('Buttons');

    const [isMinimized, setIsMinimized] = useState(false);
    const [activeStrategy, setActiveStrategy] = useState(Sphere_Strategies.FILL_INSIDE);
    const [{ activeViewportId }, viewportGridService] = useViewportGrid();

    const [segments, setSegments] = useState([]);
    const [isEraseOnlyMinimized, setIsEraseOnlyMinimized] = useState(true);
    const [eraseFocus, setEraseFocus] = useState('All');

    const handleSegmentClick = (args, sphereInstance) => {
        const {label, segmentIndex} = args;
        sphereInstance.setConfiguration({eraseFocusIndex: segmentIndex});

        setEraseFocus(label);
    }

    const setSegmentsForToolGroup = (sphereInstance) => {
        const segmentation = segmentationService.getSegmentations().filter(s => s.isActive)[0];
        let segments = [{label: 'All', segmentIndex: 0}, ...segmentation.segments.filter(s => s)];
        segments = segments.map(s => ({
            ...s,
            onClick: args => handleSegmentClick(args, sphereInstance)
        }));
        setSegments(segments);
    }

    //Update "erase more" list on segment modified
    useEffect(() => {
        const viewport = cornerstoneViewportService.getCornerstoneViewport(activeViewportId);
        const toolGroup = csTools.ToolGroupManager.getToolGroupForViewport(viewport.id, viewport.renderingEngineId);
        const sphereInstance = toolGroup.getToolInstance('SphereScissor');

        const added = segmentationService.EVENTS.SEGMENTATION_ADDED;
        const updated = segmentationService.EVENTS.SEGMENTATION_UPDATED;
        const removed = segmentationService.EVENTS.SEGMENTATION_REMOVED;
        const subscriptions = [];

        [added, updated, removed].forEach(evt => {
            segmentationService.subscribe(evt, () => {
                setSegmentsForToolGroup(sphereInstance);
            });
        });

        return () => {
            subscriptions.forEach(unsub => {
                unsub();
            });
        };
    }, []);

    useEffect(() => {
        const viewport = cornerstoneViewportService.getCornerstoneViewport(activeViewportId);
        const toolGroup = csTools.ToolGroupManager.getToolGroupForViewport(viewport.id, viewport.renderingEngineId);
        const sphereInstance = toolGroup.getToolInstance('SphereScissor');

        setActiveStrategy(sphereInstance.configuration.activeStrategy);
        setSegmentsForToolGroup(sphereInstance);
    }, []);

    useEffect(() => {
        const viewport = cornerstoneViewportService.getCornerstoneViewport(activeViewportId);
        const toolGroup = csTools.ToolGroupManager.getToolGroupForViewport(viewport.id, viewport.renderingEngineId);
        const sphereInstance = toolGroup.getToolInstance('SphereScissor');

        sphereInstance.setConfiguration({ activeStrategy: activeStrategy });
    }, [activeStrategy]);

    return (
        <div className="flex flex-col min-h-0 font-inter font-[300]">
            <div className="bg-primary-dark">
                <div
                    className="flex cursor-pointer items-center"
                    onClick={() => setIsMinimized(!isMinimized)}
                >
                    <Icon
                        name="panel-group-open-close"
                        className={classNames(
                            'w-5 h-5 text-white transition duration-300 cursor-pointer',
                            {
                                'transform rotate-90': !isMinimized,
                            }
                        )}
                    />
                    <span className="text-[#d8d8d8] text-[12px] font-[300]">
                        {'Sphere Scissors Tool Edit'}
                    </span>
                </div>
                {/* active segmentation */}
                {!isMinimized && (
                    <div className="pl-2 pr-2 pb-[9px]" style={{display:'flex', flexDirection:"column"}}>
                        <span className="text-[#d8d8d8] text-[12px] font-[300] pb-[9px]">
                          {'Mode'}
                        </span>
                        <div
                            className="text-[#d8d8d8] text-[12px] font-[300] pb-[9px]"
                            style={{display:'inline-flex', justifyContent:'space-evenly'}}
                        >
                            <Button
                                fullWidth={true}
                                size={'small'}
                                color={activeStrategy === Sphere_Strategies.FILL_INSIDE ? 'primary' : 'secondary'}
                                onClick={() => {setActiveStrategy(Sphere_Strategies.FILL_INSIDE)}}
                            >{'Fill'}</Button>
                            <Button
                                fullWidth={true}
                                size={'small'}
                                color={activeStrategy === Sphere_Strategies.ERASE_INSIDE ? 'primary' : 'secondary'}
                                onClick={() => {setActiveStrategy(Sphere_Strategies.ERASE_INSIDE)}}
                            >{'Erase'}</Button>
                        </div>
                        <div className="flex flex-col min-h-0 font-inter font-[300]">
                            <div className="bg-primary-dark">
                                <div
                                    className="flex cursor-pointer items-center"
                                    onClick={evt => {
                                        evt.stopPropagation();
                                        setIsEraseOnlyMinimized(!isEraseOnlyMinimized);
                                    }}
                                >
                                    <Icon
                                        name="panel-group-open-close"
                                        className={classNames(
                                            'w-5 h-5 text-white transition duration-300 cursor-pointer',
                                            {
                                                'transform rotate-90': !isEraseOnlyMinimized,
                                            }
                                        )}
                                    />
                                    <span className="text-[#d8d8d8] text-[12px] font-[300]">
                                        {'Erase Options'}
                                    </span>
                                </div>
                                {/* active segmentation */}
                                {!isEraseOnlyMinimized && (
                                    <div className="pl-2 pr-2" style={{display:'flex', flexDirection:"column"}}>
                                        <span className="text-[#d8d8d8] text-[12px] font-[300] pb-[6px]">
                                          {'Erase focus on:'}
                                        </span>
                                        <ListMenu
                                            items={segments}
                                            renderer={args => listItemRenderer({...args, t, eraseFocus})}
                                        />
                                    </div>
                                )}
                            </div>
                        </div>
                    </div>
                )}
                <div className="h-[6px] bg-black "></div>
            </div>
        </div>
    );
}