import React, { useEffect, useState, useCallback } from 'react';
import classNames from "classnames";
import Icon from '../../../../../platform/ui/src/components/Icon'
import DualInputRange from '../../../../../platform/ui/src/components/DualInputRange';
import InputRange from '../../../../../platform/ui/src/components/InputRange'
import {Button, useViewportGrid} from "@ohif/ui";
import * as csTools from '@cornerstonejs/tools';
import {cache, utilities as csUtils} from "@cornerstonejs/core";
import { ImageVolume } from '@cornerstonejs/core/src/cache/classes/ImageVolume';
import {computeNormal} from "@kitware/vtk.js/Common/DataModel/Triangle";
const { Enums: csToolsEnums } = csTools
const { MouseBindings, Events } = csToolsEnums;

const EVENT_LISTENERS = []

export default function RectangleROIStartEndThresholdToolEdit({ segmentationService, cornerstoneViewportService }) {
    const [isMinimized, setIsMinimized] = useState(false);
    const [{ activeViewportId }, viewportGridService] = useViewportGrid();

    const [thresholdValue, setThresholdValue] = useState([-1000, 3095]);
    const [slicesToPropagate, setSlicesToPropagate] = useState(0);
    const [pipetteMode, setPipetteMode] = useState({active: false, minimum: true});

    useEffect(() => {
        const viewport = cornerstoneViewportService.getCornerstoneViewport(activeViewportId);
        const toolGroup = csTools.ToolGroupManager.getToolGroupForViewport(viewport.id, viewport.renderingEngineId);
        const rectangleROIInstance = toolGroup.getToolInstance('RectangleROIStartEndThreshold');

        setSlicesToPropagate(rectangleROIInstance.configuration.numSlicesToPropagate);
    }, []);

    useEffect(() => {
        const viewport = cornerstoneViewportService.getCornerstoneViewport(activeViewportId);
        const toolGroup = csTools.ToolGroupManager.getToolGroupForViewport(viewport.id, viewport.renderingEngineId);
        const rectangleROIInstance = toolGroup.getToolInstance('RectangleROIStartEndThreshold');

        rectangleROIInstance.setConfiguration({ numSlicesToPropagate: slicesToPropagate });
        console.log('SET CONFIGURATION', slicesToPropagate);
    }, [slicesToPropagate]);

    const setThresholdCallback = (evt, minimum) => {
        //If left click
        if(evt.detail.mouseButton === 1) {
            const viewport = cornerstoneViewportService.getCornerstoneViewport(activeViewportId);
            const worldPoints = evt.detail.currentPoints.world;
            const imageData = viewport.getImageData();
            const index = imageData.imageData.worldToIndex(worldPoints).map(Math.round);

            const {scalarData, dimensions} = imageData;
            if (csUtils.indexWithinDimensions(index, dimensions)) {
                const yMultiple = dimensions[0];
                const zMultiple = dimensions[0] * dimensions[1];

                const value = scalarData[index[2] * zMultiple + index[1] * yMultiple + index[0]];
                if (minimum) {
                    setThresholdValue([value, thresholdValue[1]]);
                } else {
                    setThresholdValue([thresholdValue[0], value]);
                }
            }
        }
    }

    useEffect(() => {
        const viewport = cornerstoneViewportService.getCornerstoneViewport(activeViewportId)
        const toolGroup = csTools.ToolGroupManager.getToolGroupForViewport(viewport.id, viewport.renderingEngineId);
        const currentElement = viewport.element;
        currentElement.addEvent = (evt, callback) => {
            if(EVENT_LISTENERS[evt]) {
                currentElement.removeEventListener(evt, EVENT_LISTENERS[evt]);
                EVENT_LISTENERS[evt] = null;
            }
            EVENT_LISTENERS[evt] = callback;
            currentElement.addEventListener(evt, callback);
        };
        currentElement.removeEvent = (evt) => {
            currentElement.removeEventListener(evt, EVENT_LISTENERS[evt]);
            EVENT_LISTENERS[evt] = null
        };

        if (pipetteMode.active) {
            toolGroup.setToolPassive('RectangleROIStartEndThreshold');
            toolGroup.setToolActive('DragProbe', {
                bindings: [
                    {
                        mouseButton: MouseBindings.Primary, // Left Click
                    },
                ],
            });
            currentElement.addEvent(Events.MOUSE_DRAG, (evt) => {setThresholdCallback(evt, pipetteMode.minimum)});
        } else {
            toolGroup.setToolPassive('DragProbe');
            toolGroup.setToolActive('RectangleROIStartEndThreshold', {
                bindings: [
                    {
                        mouseButton: MouseBindings.Primary, // Left Click
                    },
                ],
            });
            currentElement.removeEvent(Events.MOUSE_DRAG);
        }
    }, [pipetteMode.active, pipetteMode.minimum]);

    const computeThreshold = () => {
        const actors = cornerstoneViewportService.getCornerstoneViewport(activeViewportId).getActors();
        const imageVolumes = actors
            .map((va) => {
                // prefer the referenceUID if it is set, since it can be a derived actor
                // and the uid does not necessarily match the volumeId
                const actorUID = va.referenceId ?? va.uid;
                return cache.getVolume(actorUID);
            })
            //filter to get only ImageVolume instances (and not StreamingImageVolume)
            .filter((iv) => !!iv);

        const selectedAnnotationUIDs = csTools.annotation.selection.getAnnotationsSelectedByToolName(
            'RectangleROIStartEndThreshold'
        ) as Array<string>;

        if (!selectedAnnotationUIDs) {
            throw new Error('No annotation selected ');
        }

        const annotationUID = selectedAnnotationUIDs[0];
        const annotation = csTools.annotation.state.getAnnotation(
            annotationUID
        ) as csTools.Types.ToolSpecificAnnotationTypes.RectangleROIStartEndThresholdAnnotation;

        if (!annotation) return

        // Todo: this only works for volumeViewport
        const ctVolume = imageVolumes.filter(iv => iv.imageIds.length)[0];
        const segmentationVolume = imageVolumes.filter(iv => !iv.imageIds.length)[0];
        const segmentations = segmentationService.getSegmentations();

        csTools.utilities.segmentation.rectangleROIThresholdVolumeByRange(
            selectedAnnotationUIDs,
            segmentationVolume,
            [
                { volume: ctVolume, lower: thresholdValue[0], upper: thresholdValue[1] },
            ],
            {
                numSlicesToProject: 0,
                overwrite: false,
                overlapType: 1,
                segmentIndex: segmentations[0].activeSegmentIndex,
            }
        );
    }

    const deleteRegion = () => {
        const selectedAnnotationUIDs = csTools.annotation.selection.getAnnotationsSelectedByToolName(
            'RectangleROIStartEndThreshold'
        ) as Array<string>;

        if (!selectedAnnotationUIDs) {
            throw new Error('No annotation selected ');
        }
        console.log('selected annotation uid', selectedAnnotationUIDs);

        const annotationUID = selectedAnnotationUIDs[0];
        console.log('CSTOOLS STATE', csTools.annotation.state);
        csTools.annotation.state.removeAnnotation(annotationUID);
        cornerstoneViewportService.getCornerstoneViewport(activeViewportId).render();
    }

    return (
        <div className="flex flex-col min-h-0 font-inter font-[300]">
            <div className="bg-primary-dark">
                <div
                    className="flex cursor-pointer items-center"
                    onClick={() => setIsMinimized(!isMinimized)}
                >
                    <Icon
                        name="panel-group-open-close"
                        className={classNames(
                            'w-5 h-5 text-white transition duration-300 cursor-pointer',
                            {
                                'transform rotate-90': !isMinimized,
                            }
                        )}
                    />
                    <span className="text-[#d8d8d8] text-[12px] font-[300]">
                        {'3D Rectangle Threshold Segmentation Tool Edit'}
                    </span>
                </div>
                {/* active segmentation */}
                {!isMinimized && (
                    <div className="pl-2 pr-2 pb-[9px]" style={{display:'flex', flexDirection:"column"}}>
                        <div className="flex items-center col-span-2" style={{flexDirection:'column', width:'100%'}}>
                            <div className="text-[#ffffff] text-[12px] pr-[9px]" style={{width:'100%'}}>{'Threshold values'}</div>
                            <DualInputRange
                                minValue={-1000}
                                maxValue={5000}
                                value1={thresholdValue[0]}
                                value2={thresholdValue[1]}
                                onChange={(vmin, vmax) => {setThresholdValue([vmin, vmax])}}
                                step={5}
                                containerClassName="mt-[4px] mb-[4px] w-full"
                                inputClassName="w-full"
                                labelClassName="text-white text-[12px] w-full"
                                unit=""
                            />
                            <div className="text-[#ffffff] text-[12px] pr-[9px] pt-[9px] mb-[3px]" style={{width:'100%'}}>{'Select threshold value with pipette for :'}</div>
                            <div className="flex flex-row justify-evenly w-full mb-[8px]">
                                <Button
                                    fullWidth={true}
                                    size={'small'}
                                    color={'secondary'}
                                    onClick={() => { setPipetteMode({active: true, minimum: true})}}
                                >{'Minimum'}</Button>
                                <Button
                                    fullWidth={true}
                                    size={'small'}
                                    color={'secondary'}
                                    onClick={() => { setPipetteMode({active: true, minimum: false})}}
                                >{'Maximum'}</Button>
                            </div>
                            {
                                pipetteMode.active && (
                                    <Button
                                        size={'small'}
                                        color={'secondary'}
                                        onClick={() => { setPipetteMode({active: false, minimum: true})}}
                                    >
                                        {'Back to annotation'}
                                    </Button>
                                )
                            }
                        </div>
                        <div className="flex items-center col-span-2" style={{width:'100%', flexDirection:'column'}}>
                            <div className="text-[#ffffff] text-[12px] pr-[9px]" style={{width:'100%'}}>{'Slices Propagation'}</div>
                            <InputRange
                                minValue={0}
                                maxValue={200}
                                value={slicesToPropagate}
                                onChange={(value) => {setSlicesToPropagate(value)}}
                                step={1}
                                containerClassName="mt-[4px] mb-[4px] w-full"
                                inputClassName="w-full"
                                labelClassName="text-white text-[12px] w-full"
                                unit=""
                            />
                        </div>
                        <div
                            className="text-[#d8d8d8] text-[12px] font-[300] pb-[9px]"
                            style={{display:'inline-flex', justifyContent:'space-evenly'}}
                        >
                            <Button
                                fullWidth={true}
                                size={'small'}
                                color={'secondary'}
                                onClick={() => {computeThreshold()}}
                            >{'Compute Threshold'}</Button>
                        </div>
                        <div
                            className="text-[#d8d8d8] text-[12px] font-[300] pb-[9px]"
                            style={{display:'inline-flex', justifyContent:'space-evenly'}}
                        >
                            <Button
                                fullWidth={true}
                                size={'small'}
                                color={'secondary'}
                                onClick={() => {deleteRegion()}}
                            >{'Delete selected region'}</Button>
                        </div>
                    </div>
                )}
                <div className="h-[6px] bg-black "></div>
            </div>
        </div>
    );
}
